body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-checkbox .f-checkbox--solid__field:not([disabled]) + .f-checkbox__label:after {
  color: white; }

.f-checkbox .f-checkbox--solid__field:not([disabled]):hover + .f-checkbox__label:after {
  color: white; }

.f-checkbox .f-checkbox--solid__field:not([disabled]):focus + .f-checkbox__label:after {
  color: white; }

.f-checkbox .f-checkbox--solid__field:not([disabled]):checked + .f-checkbox__label:before {
  color: #6fcf97;
  background-color: currentColor; }

.f-checkbox .f-checkbox--solid__field:not([disabled]):hover + .f-checkbox__label:before {
  color: #6fcf97; }

.f-checkbox .f-checkbox--solid__field:not([disabled]):focus + .f-checkbox__label:before {
  color: #6fcf97; }
